.navbar {
    background: orangered !important;
    font-family: 'Coiny', cursive, serif;
    font-size: 40px;
}

.navbar li {
    text-align: center;
    display: inline-block;
}

.title {
    width: 100%;
    text-align: center;
    font-size: 3rem;
    text-decoration: underline;
    color: black;
    font-family: 'Coiny', cursive, serif;
    margin-bottom: 50px;
  }
.card {
    background: orangered;
    border-radius: 2px;
    height: 350px;
    width: 200px;
    margin: 15px 30px;
    position: relative;
    text-align: left;
  }
  
  .card > .img-container {
    height: 100%;
    overflow: hidden;
    text-align: center;
    padding: auto;
   
  }
  .card > .img-container > img {
    width: 150px;
    height: 90%;
    margin: auto;
  }
  
  .card > .content {
    padding-left: 1rem;
    padding-right: 1rem;
    font-size: 15px;
    background: white;
  }
  
  .card > .content > ul {
    list-style-type: none;
    font-family: 'Coiny', cursive, serif;
  }
  
  .card > .content > ul > li {
    margin-top: 18px;
    font-family: 'Coiny', cursive, serif;
  }
  
/* For image containers */
.wrapper {
    padding-top: 50px;
    background: #ECEFF1;
    height: 100%;
    display: flex;
    flex-flow: row wrap;
    padding: 20px;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
  }
  
/* For main background image */
.wrapper {
  background: url(/Clicky-Game/static/media/background.29557695.jpg) no-repeat fixed center;
  background-repeat: no-repeat;
  background-size: 100%;

}

 
